<template>
  <div class="bottom-nav">
    <ul>
      <li :class="{active:index===curIndex}" v-for="(list,index) in navConfig" :key="index">
        <router-link :to="{name:list.href,query:{index:index}}">
          <i :class="list.icon"></i>
          <div>{{list.name}}</div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
let a;
let navConfig = [
  {
    name: "扫码干衣",
    icon: "icon-dry",
    href: "index"
  },
  {
    name: "商城",
    icon: "icon-mall",
    href: "mall"
  },
  {
    name: "我的",
    icon: "icon-my",
    href: "my"
  }
];
export default {
  data() {
    return {
      navConfig,
      curIndex: parseInt(a) || 0
    };
  },
  watch: {
    $route(to, from) {
      a = this.$route.query.index;
      this.curIndex = a;
    }
  }
};
</script>

<style>
.bottom-nav {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 0;
  background-color: #fff;
  z-index: 9999;
  -o-border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2
    stretch;
  border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
  border-top: 2px solid #e5e5e5;
}
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .bottom-nav {
    border-top-width: 1px;
  }
}
.bottom-nav ul {
  position: relative;
  height: 100%;
  zoom: 1;
}
.bottom-nav ul:after {
  content: "";
  display: table;
  clear: both;
}
.bottom-nav li {
  display: block;
  float: left;
  width: 33.3%;
  height: 100%;
}
.bottom-nav a {
  display: block;
  width: 100%;
  height: 100%;
  padding-top: 8px;
  text-align: center;
  font-size: 10px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(149, 149, 149, 1);
  line-height: 13px;
  text-decoration: none;
}
.bottom-nav [class^="icon-"] {
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
  margin-bottom: 4px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.bottom-nav .icon-dry {
  background-image: url(../assets/images/nav_dry@2x.png);
}
.bottom-nav .icon-mall {
  background-image: url(../assets/images/nav_mall@2x.png);
}
.bottom-nav .icon-my {
  background-image: url(../assets/images/nav_my@2x.png);
}
.bottom-nav .active a {
  color: rgba(72, 68, 67, 1);
}
.bottom-nav .active .icon-dry {
  background-image: url(../assets/images/nav_dry_hover@2x.png);
}
.bottom-nav .active .icon-mall {
  background-image: url(../assets/images/nav_mall_hover@2x.png);
}
.bottom-nav .active .icon-my {
  background-image: url(../assets/images/nav_my_hover@2x.png);
}
</style>


